@import '../../styles/breakpoint.scss';

.benefits .container {
    @include mobile {
        padding: 0;
    }

    ;
}

.benefits_inner {
    padding: 64px;
    background-color: var(--background-color);
    border-radius: 32px;
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    gap: 40px;
    padding-bottom: 0;

    @include big-desktop {
        padding: calc(64 / 14.4 * 1vw);
        padding-bottom: 0;
        border-radius: calc(32 / 14.4 * 1vw);
        gap: calc(40 / 14.4 * 1vw);
    }

    @include mobile {
        grid-template-columns: 1fr;
        padding-top: calc(64 / 3.93 * 1vw);
        padding-left: calc(17 / 3.93 * 1vw);
        padding-right: calc(17 / 3.93 * 1vw);
        gap: calc(32 / 3.93 * 1vw);
        border-radius: calc(24 / 3.93 * 1vw);
    }
}

.benefits_inner .project_title {
    text-align: left;
}

.benefits_inner_item {

    &.--desktop {

        @include mobile {
            order: 10;
            position: relative;

            &::after {
                position: absolute;
                bottom: calc(45 / 3.93 * 1vw);
