            }
        }
    }

    & .project_title.--desktop {
        @include mobile {
            display: none;
        }
    }

    &.--mobile {
        display: none;

        @include mobile {
            display: block;
        }

        & .project_title {
            text-align: center;
            max-width: calc(257 / 3.93 * 1vw);
            display: block;
            margin-left: auto;
            margin-right: auto;
        }
    }

}

.benefits_inner_item:first-child {
    grid-column: 3 span;
    position: relative;

    @include mobile {
        grid-column: unset;
    }
}

