    @include mobile {
        display: none;
    }
}

.benefits_inner_item:last-child {
    grid-column: 5 span;
    padding-left: 70px;
    display: flex;
    gap: 40px;

    @include big-desktop {
        padding-left: calc(70 / 14.4 * 1vw);
        gap: calc(40 / 14.4 * 1vw);
    }

    @include mobile {
        grid-column: unset;
        flex-direction: column;
        gap: calc(12 / 3.93 * 1vw);
        padding-left: 0;
    }

    &::after {
        @include mobile {}
    }
}

.beneefits_item_list li {
    position: relative;
    z-index: 2;
}

.beneefits_item_list li:not(:last-child)::after {
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    height: 100%;
    width: 2px;
    content: '';
    z-index: 1;
    background-color: #fff;

    @include big-desktop {
        width: calc(2/ 14.4 * 1vw);
    }

    @include mobile {
        display: none;
    }
}

.beneefits_item_list:first-child li:last-child::after {
    position: absolute;
    top: 100%;
    left: 50%;
    content: '';
    z-index: 1;
    background-image: url('data:image/svg+xml,%3Csvg%20width%3D%22346%22%20height%3D%2287%22%20viewBox%3D%220%200%20346%2087%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%20%3Cpath%20d%3D%22M1%200V70C1%2078.8366%208.16344%2086%2017%2086H329C337.837%2086%20345%2078.8366%20345%2070V19.9404%22%20stroke%3D%22white%22%20stroke-width%3D%222%22%20%2F%3E%3C%2Fsvg%3E');
    width: 346px;
    height: 87px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;

    @include big-desktop {
        width: calc(346 / 14.4 * 1vw);
        height: calc(87 / 14.4 * 1vw);
    }

    @include mobile {
        display: none;
    }
}

.beneefits_item_list li .content_wrapper {
    border: 1px solid #d2dade;
    position: relative;
    border-radius: 24px;
    background-color: #fff;
    padding: 24px 20px;
    display: grid;
    grid-template-columns: 50px 1fr;

    @include big-desktop {
        border-width: calc(1 / 14.4 * 1vw);
        border-radius: calc(24 / 14.4 * 1vw);
        padding: calc(24 / 14.4 * 1vw) calc(20 / 14.4 * 1vw);
        grid-template-columns: calc(50 / 14.4 * 1vw) 1fr;
    }

    @include mobile {
        border-width: calc(1 / 3.93 * 1vw);
        border-radius: calc(16 / 3.93 * 1vw);
        padding: calc(20 / 3.93 * 1vw);
        grid-template-columns: calc(32 / 3.93 * 1vw) 1fr;
    }
}

.beneefits_item_list {
    display: flex;
    flex-direction: column;
    gap: 32px;

    @include big-desktop {
        gap: calc(32 / 14.4 * 1vw);
    }

    @include mobile {
        gap: calc(12 / 3.93 * 1vw);
    }
}

.benefits_inner_item:first-child {
    display: flex;
    flex-direction: column;
    gap: 50px;

    @include big-desktop {
        gap: calc(50 / 14.4 * 1vw);
    }
}

.beneefits_item_list li p {
    margin: 0;
    grid-row: 2;
    grid-column: 2;

    @include mobile {
        max-width: calc(212 / 3.93 * 1vw);
    }
}

.beneefits_item_list_title {
    margin: 0;
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 20px;
    line-height: 100%;
    letter-spacing: -0.03em;
    color: #283034;
    margin-bottom: 8px;
    grid-column: 2;

    @include big-desktop {
        font-size: calc(20 / 14.4 * 1vw);
        margin-bottom: calc(8 / 14.4 * 1vw);
    }

    @include mobile {
        font-size: calc(18 / 3.93 * 1vw);
        margin-bottom: calc(8 / 3.93 * 1vw);
        max-width: calc(212 / 3.93 * 1vw);
    }
}

.beneefits_item_list li .content_wrapper::before {
    position: absolute;
    left: 20px;
    top: 50%;
    transform: translateY(-50%);
    content: '';
    width: 48px;
    height: 48px;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url('data:image/svg+xml,%3Csvg%20width=%2248%22%20height=%2248%22%20viewBox=%220%200%2048%2048%22%20fill=%22none%22%20xmlns=%22http://www.w3.org/2000/svg%22%3E%20%20%20%3Cpath%20d=%22M14.9493%2035.9173C14.7494%2035.9674%2014.536%2035.974%2014.3255%2035.9318C13.8376%2035.8344%2013.4434%2035.4911%2013.2986%2035.0382L8.7593%2020.8218C8.54194%2020.1416%208.94516%2019.428%209.66062%2019.2279C10.3769%2019.0241%2011.1313%2019.4172%2011.3482%2020.0969L15.1582%2032.0277L32.8302%2012.0098C33.3086%2011.4687%2034.1613%2011.4013%2034.7358%2011.859C35.3106%2012.3172%2035.3891%2013.1274%2034.911%2013.6684L15.6339%2035.5051C15.4532%2035.7099%2015.2127%2035.8514%2014.9493%2035.9173Z%22%20fill=%22%2369BFF3%22%20/%3E%20%3C/svg%3E');
    background-size: contain;

    @include big-desktop {
        left: calc(20 / 14.4 * 1vw);
        width: calc(48 / 14.4 * 1vw);
        height: calc(48 / 14.4 * 1vw);
    }

    @include mobile {
        width: calc(32 / 3.93 * 1vw);
        height: calc(32 / 3.93 * 1vw);
        left: calc(20 / 3.93 * 1vw);
        transform: none;
        top: calc(20 / 3.93 * 1vw);
    }
}

.beneefits_item_list:last-child {
    margin-top: 92px;

    @include big-desktop {
        margin-top: calc(92 / 14.4 * 1vw);
    }

    @include mobile {
        margin-top: 0;
    }
}

.benefits_inner_item_img {
    max-width: 365px;
    display: block;
    margin-left: auto;

    @include big-desktop {
        max-width: calc(365 / 14.4 * 1vw);
    }

    @include mobile {
        margin-left: calc(62 / 3.93 * 1vw);
        max-width: calc(227 / 3.93 * 1vw);
        // height: calc(227 / 3.93 * 1vw);
        margin-top: calc(-65 / 3.93 * 1vw);
        position: relative;
        z-index: 5;
    }
}